<template>
  <input
    :type="type"
    :placeholder="placeholder"
    :value="value"
    @input="$emit('input', $event.target.value)"
    class="form-input mt-1 block w-full border-2 border-transparent"
  />
</template>
<script>
export default {
  props: {
    type: String,
    placeholder: String,
    value: { type: String, default: '' },
  },
};
</script>
