<template>
  <form class="px-8 pb-16 mt-56 w-full md:w-1/3">
    <v-custom-label>
<!--      <template #label>Name</template>-->
      <template #input>
        <v-custom-input
          :class="errors.name"
          v-model="formInput.name"
          type="text"
          placeholder="Name"
        />
      </template>
    </v-custom-label>
    <v-custom-label>
<!--      <template #label>Subject</template>-->
      <template #input>
        <v-custom-input
          :class="errors.subject"
          v-model="formInput.subject"
          type="text"
          placeholder="Subject"
        />
      </template>
    </v-custom-label>
    <v-custom-label>
<!--      <template #label>Email</template>-->
      <template #input>
        <v-custom-input
          :class="errors.email"
          v-model="formInput.email"
          type="email"
          placeholder="Enter your email"
        />
      </template>
    </v-custom-label>
    <v-custom-label>
<!--      <template #label>Textarea</template>-->
      <template #input>
        <textarea
          :class="errors.text"
          class="form-textarea mt-1 block w-full"
          rows="3"
          placeholder="Write your message here"
          v-model="formInput.text"
        ></textarea>
      </template>
    </v-custom-label>

    <v-request :method="resource" #request="{ isLoading, error, request }">
      <button
        class="w-full flex justify-center items-center bg-blue-700 rounded-sm p-3 mt-4 text-white text-lg"
        @click="(e) => onSubmit(e, request)"
      >
        <spinner v-if="isLoading" class="flex items-center justify-between" />
        <div v-if="isLoading" class="ml-1 text-sm">Sending...</div>
        <div v-else>Send</div>
      </button>
    </v-request>
  </form>
</template>

<script>
import VCustomLabel from '@/components/forms/VCustomLabel';
import VCustomInput from '@/components/forms/VCustomInput';
import VRequest from '@/components/body/contact/VRequest';
import { emailService } from '@/services/email.js';
import Spinner from 'vue-simple-spinner';

export default {
  components: {
    VCustomLabel,
    VCustomInput,
    VRequest,
    Spinner,
  },
  data() {
    return {
      formInput: { name: '', subject: '', email: '', text: '' },
      errors: {},
    };
  },

  // computed: {
  //   resource: options => emailService(options),
  // },

  methods: {
    resource: options => emailService(options),
    onSubmit(e, request) {
      e.preventDefault();
      this.errors = Object.entries(this.formInput).reduce(
        (acumulator, [key, value]) => {
          if (!value) {
            return { ...acumulator, [key]: 'border-2 border-red-600' };
          }
          return acumulator;
        },
        {},
      );

      if (Object.keys(this.errors).length === 0) {
        request.call(this, this.formInput);
        this.errors = {};
      }
    },
  },
};
</script>
