<template>
  <div class="my-4">
    <div
      v-if="Object.keys(mainConfig).length > 0"
      class="relative before:block before:absolute before:aspect-ratio-16/9 before:empty-content before:absolute w-full my-0 mx-auto"
    >
      <Hooper :settings="{ ...hooperSettings }" class="w-full h-full">
        <Slide v-for="photo in photos" :key="photo">
          <VImage :src="`${imageURL}/assets/albums/${folder}/${photo}`" />
        </Slide>
      </Hooper>
    </div>
    <div
      class="font-normal font-lato py-6 px-12 text-gray-900 text-lg md:text-xl lg:px-32 lg:text-2xl text-center uppercase"
    >
      A precious Moment, a lovely image, can be captured and kept forever.
    </div>
  </div>
</template>

<script>
import { Hooper, Slide } from 'hooper';
import 'hooper/dist/hooper.css';
import { mapGetters } from 'vuex';
import VImage from '../albums/VImage';
import { IMAGE_URL } from '@/config';

export default {
  name: 'Main',
  components: {
    VImage,
    Hooper,
    Slide,
  },
  data() {
    return {
      hooperSettings: {
        autoPlay: true,
        itemsToShow: 1,
        infiniteScroll: true,
        wheelControl: false,
        playSpeed: 2500,
      },
    };
  },

  computed: {
    ...mapGetters({ mainConfig: 'getFrontPageSlide' }),

    imageURL() {
      return IMAGE_URL;
    },
    photos() {
      return this.mainConfig.photos;
    },

    folder() {
      return this.mainConfig.folder;
    },
  },
};
</script>
