<template>
  <cool-light-box :index="index" :items="items" @close="index = null" />
</template>

<script>
import CoolLightBox from 'vue-cool-lightbox';
import 'vue-cool-lightbox/dist/vue-cool-lightbox.min.css';
import { IMAGE_URL } from '@/config';

export default {
  components: {
    CoolLightBox,
  },
  data() {
    return {
      index: null,
      items: [],
    };
  },
  
  methods: {
    modalOpenEventHandler({ index, data }) {
      this.isOpen = !this.isOpen;
      const { folder, photos } = data;
      this.items = photos.map(p => `${IMAGE_URL}/assets/albums/${folder}/${p}.jpg`);

      this.index = index;
    },
  },
  mounted() {
    this.$eventBus.$on('open-modal', this.modalOpenEventHandler);
  },
};
</script>
