<template>
  <div class="flex items-center justify-center mx-12 lg:mx-24">
    <div id="instagram" class="w-8 h-8 ml-4 cursor-pointer" @click="onClick">
      <img src="@/../public/assets/icons/insta.png" alt="" />
    </div>
    <div id="facebook" class="w-8 h-8 ml-4 cursor-pointer" @click="onClick">
      <img src="@/../public/assets/icons/fb.png" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'SocialMedia',
  methods: {
    onClick(event) {
      if (event.target.parentElement.id === 'facebook') {
        window.open('https://www.facebook.com/anatopalaphotography/');
      } else if (event.target.parentElement.id === 'instagram') {
        window.open('https://instagram.com/anatopalaphotography');
      }
    },
  },
};
</script>
