<template>
  <img ref="img" :src="`${src}ph.jpg`" alt />
</template>

<script>
export default {
  props: {
    src: String,
  },

  mounted() {
    var newImg = new Image();
    const that = this;
    newImg.onload = function() {
      that.$refs.img.src = this.src;
    };

    newImg.src = `${this.src}.jpg`;
  },
};
</script>

