import Vue from "vue";
import Vuex from "vuex";
import { isMobile } from "mobile-device-detect";
import { albumModule } from "./modules/album";

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    albumModule
  },
  state: {
    root: { isMobile, isPortraitMode: true, isSideMenuShown: false }
  },
  getters: {
    isMobile: state => state.root.isMobile
  },

  mutations: {
    updateScreenSize(state, payload) {
      state.root.isSideMenuShown = payload <= 480;
    },

    orientationChange(state, payload) {
      state.root.isPortraitMode = payload.isPortraitMode;
    }
  }
});
