<template>
  <div>
    <slot name="request" v-bind="{ isLoading, error, request }" />
  </div>
</template>

<script>
export default {
  props: {
    method: {
      type: Function || Promise,
      required: true,
    },
  },
  data() {
    return {
      isLoading: false,
      error: null,
    };
  },
  methods: {
    request(options) {
      this.isLoading = true;
      return this.method(options)
        .then(({ status }) => {
          if (status === 200) {
            this.isLoading = false;
          }
        })
        .catch(err => {
          this.error = err;
        });
    },
  },
};
</script>

