<template>
  <div
    class="fixed top-0 md:hidden flex z-40 flex-col h-screen w-full bg-white text-gray-900 overflow-hidden"
  >
    <div
      class="flex items-center justify-center relative h-24 py-3 px-2 flex-grow-0"
    >
      <SocialMedia />
    </div>
    <div class="flex flex-grow flex-shrink flex-col overflow-y-auto">
      <div
        :key="item"
        class="text-center text-3xl my-10"
        v-for="item in elements"
        @click="() => onClickHandler(item)"
      >
        {{ item }}
      </div>
    </div>
  </div>
</template>

<script>
import { menu } from '../../utils';
import SocialMedia from '../header/links/SocialMedia';

export default {
  name: 'RightMenu',
  components: { SocialMedia },
  data() {
    return {
      elements: menu,
    };
  },
  methods: {
    onClickHandler(item) {
      this.opened = !this.opened;
      this.$emit('item-clicked');
      this.$router.push(`/${item.toLowerCase().replace(/\s/g, '')}`);
    },
  },
};
</script>
