import Vue from 'vue';
import VueRouter from 'vue-router';
import Albums from '../components/body/albums/Albums';
import Main from '../components/body/main/Main';
import Error from '../components/body/error/Error';
import Album from '../components/body/albums/Album';
import VContact from '@/components/body/contact/VContact';
import About from '@/components/body/about/About';

Vue.use(VueRouter);

export const router = new VueRouter({
  mode: 'history',
  routes: [
    // dynamic segments start with a colon
    { path: '/', /*name: "home",*/ component: Main },
    { path: '/home', /*name: "about",*/ component: Main },
    { path: '/about', /*name: "about",*/ component: About },
    { path: '/contact', /*name: "contact",*/ component: VContact },
    { path: '/gallery', /*name: "gallery",*/ component: Albums },
    { path: '/gallery/:id', /*name: "album",*/ component: Album, props: true },
    { path: '*', name: '*', component: Error },
  ],
});
