import store from '../store/index';

export const menu = ['Home', 'About', 'Gallery', 'Contact'];

export const resize = throttle(() => {
  calcBodyFont();
  store.commit('updateScreenSize', window.innerWidth);
}, 400);

function throttle(fn, wait, immediate = false) {
  let timeout = null;
  let initialCall = true;

  return function() {
    const callNow = initialCall && immediate;
    function later() {
      fn.call(this, arguments);
      timeout = null;
    }

    if (callNow) {
      initialCall = false;
      later();
    }

    if (!timeout) {
      timeout = setTimeout(later, wait);
    }
  };
}

export function orientationChange() {
  const { orientation } = window;

  if (Math.abs(orientation) === 90) {
    store.commit('orientationChange', { isPortraitMode: false });
  } else {
    store.commit('orientationChange', { isPortraitMode: true });
  }
}

export function initSizes() {
  resize();
  orientationChange();
}

function calcBodyFont() {
  const element = document.getElementById('body');

  const widthToHeight = 16 / 9;
  let newWidth = window.innerWidth;
  let newHeight = window.innerHeight;
  const newWidthToHeight = newWidth / newHeight;

  if (newWidthToHeight > widthToHeight) {
    newWidth = newHeight * widthToHeight;
  } else {
    newHeight = newWidth / widthToHeight;
  }
  element.style.fontSize = newWidth / 1300 + 'em';
}

export function add(a, b) {
  return a + b;
}
