export const albumModule = {
  state: { albumSelectedId: -1, config: [] },
  mutations: {
    setConfig(state, config) {
      state.config = config;
    }
  },

  actions: {
    setConfig({ commit }, { config }) {
      commit("setConfig", config);
    }
  },

  getters: {
    getConfig(state) {
      return state.config.slice(1);
    },

    getFrontPageSlide(state) {
      return state.config.length > 0 ? state.config[0] : {};
    }
  }
};
