<template>
  <div
    class="relative pr-16 pt-6 float-right w-0 h-0 z-50 md:hidden bg-transparent"
  >
    <div
      class="flex flex-wrap justify-between items-center w-10 h-10 py-c2 cursor-pointer"
      @click="onClickEventHandler"
    >
      <v-custom-line>
        <v-line
          class="transition-transform transition-250 origin-right"
          :class="{
            ' rotate-reverse-45 ': clicked,
          }"
        />
      </v-custom-line>
      <v-custom-line class="flex justify-end">
        <v-line
          class="transition-opacity transition-100 "
          :class="{
            'opacity-0': clicked,
          }"
        />
      </v-custom-line>
      <v-custom-line>
        <v-line
          class="transition-transform transition-250 origin-right"
          :class="{
            'rotate-45': clicked,
          }"
        />
      </v-custom-line>
    </div>
  </div>
</template>

<script>
import VLine from './VLine';
import VCustomLine from './VCustomLine';

export default {
  name: 'v-button',
  components: { VLine, VCustomLine },
  props: {
    clicked: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    onClickEventHandler() {
      this.$emit('click');
    },
  },
};
</script>
