<template>
  <div
    class="hidden md:flex flex-grow justify-evenly items-center font-lato text-xl xl:text-2xl text-gray-900 mx-10 3xl:mx-48"
  >
    <div
      :key="element"
      :id="element.toLowerCase().replace(/\s/g, '')"
      class="py-2 px-6 uppercase cursor-pointer hover:border-gray-900 border-2 border-transparent"
      v-for="element in elements"
      v-on:click="() => onClick(element)"
    >
      {{ element }}
    </div>
  </div>
</template>

<script>
import { menu } from '../../../utils';

export default {
  name: 'NavBar',
  data() {
    return {
      elements: menu,
    };
  },

  methods: {
    onClick(element) {
      this.$router.push({
        path: `/${element.toLowerCase().replace(/\s/g, '')}`,
      });
    },
  },
};
</script>
