<template>
  <div class="fixed w-full">
    <v-button :clicked="opened" @click="onClickHandler" />
    <transition name="move">
      <v-drawer v-if="opened" @item-clicked="onClickHandler" />
    </transition>
  </div>
</template>

<script>
import VButton from './Button/VButton';
import VDrawer from '@/components/mobile/VDrawer';
export default {
  components: {
    VButton,
    VDrawer,
  },
  data() {
    return {
      opened: false,
    };
  },
  methods: {
    onClickHandler() {
      this.opened = !this.opened;
    },
  },
};
</script>

<style lang="scss">
.move-enter-active {
  transition: transform 200ms ease-in;
}

.move-enter,
.move-leave-to {
  transform: translateX(100%);
}
.move-leave-active {
  transition: transform 200ms ease-out;
}
</style>
