<template>
  <div
    class=""
    v-if="this.config.length > 0"
  >
    <v-image-wrapper
      :key="photo"
      v-for="(photo, index) in data.photos"
      :src="`${imageURL}/assets/albums/${data.folder}/${photo}`"
      @click.native="() => onClickHandler(index)"
      class="mx-auto md:inline-block"
    />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import VImageWrapper from './VImageWrapper';
import { IMAGE_URL } from '@/config';

export default {
  name: 'Album',
  components: {
    VImageWrapper,
  },

  props: {
    id: String,
  },

  computed: {
    ...mapGetters({ config: 'getConfig' }),
    imageURL() {
      return IMAGE_URL
    },

    data: function() {
      return this.config.find(c => c.id == this.id);
    },
  },

  methods: {
    onClickHandler(index) {
      const { data } = this;
      this.$eventBus.$emit('open-modal', { index, data });
    },
  },
};
</script>
