<template>
  <div
    class="flex flex-col relative w-11/12 md:w-1/2 cursor-pointer relative before:aspect-ratio-4/3 before:block before:empty-content"
    @click="onClick"
  >
    <div
      class="flex flex-col absolute p-2 top-0 left-0 right-0 bottom-0 font-lato text-gray-900 text-xl md:p-10"
    >
      <img class="flex-grow-0 hover:shadow-2xl" :src="imgUrl || ''" />
      <div class="flex flex-grow justify-center items-center">
        {{ name }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'AlbumPreview',
  props: {
    id: Number,
    imgUrl: String,
    name: String,
    photos: Array,
  },

  methods: {
    ...mapActions(['selectAlbum']),
    onClick() {
      this.$router.push({ path: `/gallery/${this.id}` });
    },
  },
};
</script>
